import React from "react";
import {
  List,
  Responsive,
  SimpleList,
  Datagrid,
  TextField,
  Filter,
  TextInput
} from "react-admin";

export const HistFilter = props => (
  <Filter {...props}>
    <TextInput label="Cari History" source="q" alwaysOn />
  </Filter>
);

export const HistoriesTransaction = props => (
  <List {...props} filters={<HistFilter />} title="History Transaksi">
    <Responsive
      small={
        <SimpleList
          primaryText={record => record.title}
          secondaryText={record => `${record.views} views`}
          tertiaryText={record =>
            new Date(record.published_at).toLocaleDateString()
          }
        />
      }
      medium={
        <Datagrid>
          <TextField source="id" />
          <TextField source="name" />
          <TextField source="status" />
        </Datagrid>
      }
    />
  </List>
);
