// in src/customer.js
import React, { Fragment } from "react";
import {
  List,
  Datagrid,
  TextField,
  Responsive,
  Create,
  SimpleForm,
  SimpleList,
  TextInput,
  Edit,
  EditButton,
  DisabledInput,
  BulkDeleteButton,
  Filter
} from "react-admin";
import ResetViewsButton from "./resetviewsbutton";

export const CustFilter = props => (
  <Filter {...props}>
    <TextInput label="Cari Pelanggan" source="q" alwaysOn />
  </Filter>
);

const CustBulkActionButtons = props => (
  <Fragment>
    <ResetViewsButton label="Reset Views" {...props} />
    {/* Add the default bulk delete action */}
    <BulkDeleteButton {...props} />
  </Fragment>
);
export const CustomerList = props => (
  <List
    {...props}
    filters={<CustFilter />}
    bulkActionButtons={<CustBulkActionButtons />}
    title="Daftar Bank Sampah"
  >
    <Responsive
      small={
        <SimpleList
          primaryText={record => record.title}
          secondaryText={record => `${record.views} views`}
          tertiaryText={record =>
            new Date(record.published_at).toLocaleDateString()
          }
        />
      }
      medium={
        <Datagrid>
          <TextField source="id" />
          <TextField source="name" />
          <TextField source="email" />
          <EditButton />
        </Datagrid>
      }
    />
  </List>
);

export const CustomerCreate = props => (
  <Create {...props}>
    <SimpleForm>
      <TextInput source="name" />
      <TextInput source="email" />
    </SimpleForm>
  </Create>
);

const Custitle = ({ record }) => {
  return <span>Customer {record ? `"${record.id}"` : ""}</span>;
};
export const CustEdit = props => (
  <Edit title={<Custitle />} {...props}>
    <SimpleForm>
      <DisabledInput source="id" />
      <TextInput source="name" />
      <TextInput source="email" />
    </SimpleForm>
  </Edit>
);
