import React, { Fragment } from "react";
import {
  List,
  Datagrid,
  SimpleList,
  TextField,
  EditButton,
  Filter,
  TextInput,
  Responsive,
  BulkDeleteButton,
  Create,
  SimpleForm
} from "react-admin";
import ResetViewsButton from "./resetviewsbutton";

export const PriceFilter = props => (
  <Filter {...props}>
    <TextInput label="Cari Sampah" source="q" alwaysOn />
  </Filter>
);

const PriceBulkActionButtons = props => (
  <Fragment>
    <ResetViewsButton label="Reset Views" {...props} />
    {/* Add the default bulk delete action */}
    <BulkDeleteButton {...props} />
  </Fragment>
);
export const PriceList = props => (
  <List
    {...props}
    filters={<PriceFilter />}
    bulkActionButtons={<PriceBulkActionButtons />}
    title="Daftar Harga Sampah"
  >
    <Responsive
      small={
        <SimpleList
          primaryText={record => record.id}
          secondaryText={record => `${record.views} views`}
        />
      }
      medium={
        <Datagrid>
          <TextField source="id" />
          <TextField source="name" />
          <TextField source="price" />
          <EditButton />
        </Datagrid>
      }
    />
  </List>
);

export const PriceCreate = props => (
  <Create {...props}>
    <SimpleForm>
      <TextInput source="name" />
      <TextInput source="price" />
    </SimpleForm>
  </Create>
);
